/* globals window, document, CS_CONFIG, EditorWatchdog, ClassicEditor, LICENSE_KEY */

// Application data will be available under a global variable `appData`.
const appData = {
	// Users data.
	users: [
		{
			id: 'user-1',
			name: 'Mex Haddox'
		},
		{
			id: 'user-2',
			name: 'Zee Croce'
		}
	],

	// The ID of the current user.
	userId: 'user-1'
};

class CommentsAdapter {
	constructor( editor ) {
		this.editor = editor;
	}

	static get requires() {
		return [ 'CommentsRepository' ];
	}

	init() {
		const usersPlugin = this.editor.plugins.get( 'Users' );
		const commentsRepositoryPlugin = this.editor.plugins.get( 'CommentsRepository' );

		// Load the users data.
		for ( const user of appData.users ) {
			usersPlugin.addUser( user );
		}

		// Set the current user.
		usersPlugin.defineMe( 'user-1' );

		commentsRepositoryPlugin.adapter = {
			addComment: () => Promise.resolve(),
			updateComment: () => Promise.resolve(),
			removeComment: () => Promise.resolve(),

			getCommentThread() {
				const initialCommentThreadData = {
					threadId: 'thread-1',
					comments: [
						{
							commentId: 'comment-1',
							authorId: 'user-2',
							content: '<p>Are we sure we want to use a made-up disorder name?</p>',
							createdAt: new Date()
						},
						{
							commentId: 'comment-2',
							authorId: 'user-2',
							content: '<p>I believe <span class="mention" data-mention="@Ted">@Ted</span> wants...</p>',
							createdAt: new Date()
						}
					]
				};

				return Promise.resolve( initialCommentThreadData );
			},
			addCommentThread: () => Promise.resolve( { comments: [] } ),
			updateCommentThread: () => Promise.resolve(),
			resolveCommentThread: () => Promise.resolve( { resolvedAt: new Date(), resolvedBy: usersPlugin.me.id } ),
			reopenCommentThread: () => Promise.resolve(),
			removeCommentThread: () => Promise.resolve()
		};
	}
}

const initialData =
	`<h2>
		<comment-start name="thread-1"></comment-start>
		Bilingual Personality Disorder
		<comment-end name="thread-1"></comment-end>
	</h2>
	<p>
		This may be the first time you hear about this made-up disorder but it actually isn’t so far from the truth.
		As recent studies show, the language you speak has more effects on you than you realize.
		According to the studies, the language a person speaks affects their cognition,
		behavior, emotions and hence <strong>their personality</strong>.
	</p>
	<figure class="image image-style-side">
		<img src="../../../assets/img/collaboration-demo-img.jpg">
		<figcaption>
			One language, one person.
		</figcaption>
	</figure>
	<p>
		This shouldn’t come as a surprise
		<a href="https://en.wikipedia.org/wiki/Lateralization_of_brain_function">since we already know</a>
		that different regions of the brain become more active depending on the activity.
		The structure, information and especially <strong>the culture</strong> of languages varies substantially
		and the language a person speaks is an essential element of daily life.
	</p>`;

const watchdog = new EditorWatchdog( ClassicEditor );
window.watchdog = watchdog;

watchdog.create( document.querySelector( '.comments-mention .editor' ), {
	initialData,
	plugins: [
		'Essentials', 'Autoformat', 'Heading', 'Bold', 'Italic', 'Link', 'List', 'EasyImage', 'Image',
		'Table', 'TableToolbar', 'ImageToolbar', 'ImageStyle', 'Comments', CommentsAdapter
	],
	toolbar: {
		items: [
			'undo', 'redo',
			'|', 'comment', 'commentsArchive',
			'|', 'heading',
			'|', 'bold', 'italic',
			'|', 'link',
			'|', 'bulletedList', 'numberedList'
		]
	},
	licenseKey: LICENSE_KEY,
	sidebar: {
		container: document.querySelector( '.comments-mention .sidebar' )
	},
	image: {
		toolbar: [
			'imageStyle:inline', 'imageStyle:block', 'imageStyle:wrapText', '|',
			'imageTextAlternative', '|', 'comment'
		]
	},
	comments: {
		editorConfig: {
			extraPlugins: [ window.MentionPlugin ],
			mention: {
				feeds: [
					{
						marker: '@',
						feed: [ '@Barney', '@Lily', '@Marry Ann', '@Marshall', '@Robin', '@Ted' ]
					}
				]
			}
		}
	},
	cloudServices: CS_CONFIG,
	ui: {
		viewportOffset: {
			top: window.getViewportTopOffsetConfig()
		}
	}
} );
